import React from 'react';
import './Form.css'
function Form(){
    return (
        <div >
            <form className="form">
                <label>file<input type="file" /></label>
                <label>Learning Rate<input type="text"/></label>
                <label>lambda<input type="text"/></label>
              
                <lable>iterations<input type="text"/></lable>
                
                <div className="inputContainer">
                <h1>optimizers</h1>
                <label class="container">Batch Normalization
                <input type="checkbox" checked="checked" />
                </label>
                <label class="container">ADAM
                <input type="checkbox"  />
                </label>
                <label class="container">Momentum
                <input type="checkbox"  />
                </label>
                <label class="container">RMS PROP
                <input type="checkbox"  />
                </label>
                <label class="container">Sochastic
                <input type="checkbox" c />
                </label>
                <label class="container">none
                <input type="checkbox"  />
                </label>
                </div>
                <div className="inputContainer">
                <h1>Algorithm</h1>
                <label>Logistic Regression<input type="radio" name="Algorithm" value="Logistic Regression"/></label>
                <label>Support Vector Machine<input type="radio" name="Algorithm" value="Support Vector Machine" /></label>
                <label>K nearest neighbour<input type="radio" name= "Algorithm" value="K nearest neighbour"/></label>
                <label>Naive Bayes<input type="radio" name="Algorithm" value="Naive Bayes"/></label>
                </div>
                
                <div>
                <h1>visualize</h1>
                <lable>YES<input type="radio" name="visualize"/></lable><br />
                <lable>NO<input type="radio" name="visualize"/></lable>
                </div>
                <button className="submit">build</button>
            </form>
        </div>
       
    )
}
export default Form